import React from "react"
import Layout from "../components/layout"
import Jumbotron from "react-bootstrap/Jumbotron"
import SEO from "../components/seo"
import Row from "react-bootstrap/Row"
import Col from "react-bootstrap/Col"
import { Link } from "gatsby"
import Img from "gatsby-image"
import IntroBackground from "../components/indexPage/IntroBackground"
import introStyles from "../components/indexPage/intro.module.scss"
import { PaperAirplaneIcon } from "@primer/octicons-react"
import { useStaticQuery, graphql } from "gatsby"

const ComingSoon = () => {
  const data = useStaticQuery(
    graphql`
      query {
        comingSoon: file(relativePath: { eq: "coming-soon.png" }) {
          childImageSharp {
            fluid(maxWidth: 730) {
              ...GatsbyImageSharpFluid
            }
          }
        }
      }
    `
  )
  return (
    <Layout>
      <SEO title="Coming Soon" />
      <section className="row mx-0" style={{ background: "#e1e1e1" }}>
        <Col style={{ paddingTop: "70px" }}>
          <h1 className="text-center pb-4">I am working on it...</h1>
        </Col>
      </section>
      <IntroBackground>
        <section>
          <Jumbotron className={`mb-0 ${introStyles.jumbotron}`}>
            <Col
              xs={12}
              sm={12}
              md={12}
              lg={8}
              xl={8}
              style={{ minHeight: "66vh", color: "#f8f9fa" }}
              className="mx-auto text-center pt-4"
            >
              <p>
                This page is under production, meanwhile you can visit my other
                blog articals:&nbsp;
                <Link
                  type="button"
                  role="button"
                  className="text-monospace"
                  style={{ color: "#564af7" }}
                  to="/Blog"
                >
                  Blog
                </Link>
              </p>
              <Row>
                <Col xs={12} sm={12} md={5} lg={4} xl={4} className="mx-auto">
                  <Img
                    className="col-lg-11 col-md-12 d-block float-right img-fluid w-100 mr-2"
                    alt="profile picture"
                    fluid={data.comingSoon.childImageSharp.fluid}
                  />
                  <Link
                    to="/Blog"
                    className="btn btn-outline-success shadow text-monospace my-4 pb-2"
                    style={{ color: "#f8f9fa" }}
                  >
                    <span className="badge badge-pill p-0">
                      <PaperAirplaneIcon size={16} aria-label="Design icon" />
                    </span>
                    &nbsp; BLOG
                  </Link>
                </Col>
              </Row>
            </Col>
          </Jumbotron>
        </section>
      </IntroBackground>
    </Layout>
  )
}

export default ComingSoon
